@keyframes spinner {
  0% {
    transform: translate3d(-50%, -50%, 0) rotate(0deg);
  }
  100% {
    transform: translate3d(-50%, -50%, 0) rotate(360deg);
  }
}

.loadingSpinnerExtraSmall,
.loadingSpinnerLarge {
  height: 12px;
  width: 12px;
  opacity: 1;
  position: relative;
  transition: opacity linear 0.5s;
}

.loadingSpinnerExtraSmall::before,
.loadingSpinnerLarge::before {
  animation: 0.5s linear infinite spinner;
  border: solid 3px #eee;
  border-bottom-color: #ccc;
  border-radius: 50%;
  content: "";
  height: 12px;
  left: 50%;
  opacity: inherit;
  position: absolute;
  top: 50%;
  transform: translate3d(-50%, -50%, 0);
  transform-origin: center;
  width: 16px;
  will-change: transform;
}

.loadingSpinnerLarge {
  height: 32px;
  width: 32px;
}

.loadingSpinnerLarge::before {
  height: 32px;
  width: 32px;
}
